import * as React from 'react';
import { graphql } from 'gatsby';
import Layout from '~src/components/Layout/Layout';

import { Artworks } from '~src/pages/__generated__/Artworks';
import useIntlHelper from '~src/i18n/helpers/useIntlHelper';
import styles from './header.module.scss';
import { FilmsFeed } from '~src/components/Sections/Filmography/FilmsFeed';

const FilmographyPage: React.FC<{ data: Artworks }> = ({ data }) => {
	const { formatMessage } = useIntlHelper();
	return (
		<Layout data={data}>
			<div className="inner">
				<header className={styles.pageHeader}>
					<h1 className="site-title  no-image">{formatMessage('navigation.films')}</h1>
				</header>
				<FilmsFeed films={data.posts} />
			</div>
		</Layout>
	);
};

export default FilmographyPage;
export const artworksQuery = graphql`
	query FilmographyPage($locale: String!) {
		posts: allMdx(
			sort: { fields: [frontmatter___date], order: DESC }
			limit: 20
			filter: { fields: { locale: { eq: $locale } }, frontmatter: { category: { eq: "filmography" } } }
		) {
			nodes {
				fields {
					slug
				}
				frontmatter {
					date(formatString: "DD MMMM YYYY")
					title
					category
					featured
					tags
					image {
						childImageSharp {
							fluid(maxWidth: 700) {
								...GatsbyImageSharpFluid_withWebp_tracedSVG
							}
						}
					}
				}
				excerpt(truncate: true, pruneLength: 200)
				timeToRead
			}
		}
	}
`;
